<template>
  <div class="user-wrap">
    <van-nav-bar
      title="密码重置"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-cell-group>
      <van-field 
        v-model="tel" 
        placeholder="请输入手机号" 
        label="手机号" 
        type="tel" 
        left-icon="phone-o"
        clearable 
        maxlength='11'
        autocomplete="off"
      />
      <van-field 
        v-model="code" 
        placeholder="请输入手机验证码" 
        label="验证码" 
        type="tel" 
        left-icon="chat-o"
        clearable 
        maxlength='6'
        autocomplete="off"
      />
      <van-field 
        v-model="password" 
        placeholder="请输入密码" 
        label="密码" 
        type="password" 
        left-icon="closed-eye"
        autocomplete="off"
      />
      <van-field 
        v-model="againPassword" 
        placeholder="请再次输入密码" 
        label="确认密码" 
        type="password" 
        left-icon="closed-eye"
        autocomplete="off"
      />
    </van-cell-group>
    <div style="margin: 16px;">
      <van-button round block type="primary" native-type="register">
        确定
      </van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'forgetPassword',
  data () {
    return {
      checked: true,
      tel: '',
      code: '',
      password: '',
      againPassword: ''
    }
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    },
    isValid () {
      if (!this.tel) {
        this.$toast.fail('请输入手机号')
        return false
      }

      if (!this.code) {
        this.$toast.fail('请输入验证码')
        return false
      }

      if (!this.tel.match(/^1\d{10}$/)) {
        this.$toast.fail('请输入正确的手机号')
        return false
      }

      if (this.password !== this.againPassword) {
        this.$toast.fail('两次密码不相同')
        return false
      }

      return true
    },
    register () {
      // if (!this.isValid) {
      //   return false
      // }
      // this.$http.register({
      //   tel: this.tel,
      //   code: this.code,
      //   password: this.password,
      //   passagainPasswordword: this.againPassword
      // }).then(res => {
      //   if (res.code === 0) {
          this.$toast.success('密码重置成功，请登录')

          setTimeout(() => {
            this.$router.push({ path: '/login' })
          }, 1000)
      //   } else {
      //     this.$toast.fail(res.message)
      //   }
      // }).catch(err => {
      //   console.log(err)
      // })
    }
  }
}
</script>



